import React from 'react';
import { graphql } from 'gatsby';
const _ = require('underscore');

const Categories = ({ data }) => {
    const node = data.allMarkdownRemark;
    const categorias = {};

    node.edges.forEach((frontmatter) => {
        if (_.has(categorias, frontmatter.categorias[0])) {
            _.uniq(categorias[frontmatter.categorias[0]].tags.push(...frontmatter.tags));
        } else {
            categorias[frontmatter.categorias[0]] = {
                tags: []
            }
            categorias[frontmatter.categorias[0]].tags = frontmatter.tags;
        }
    })

    console.log(categorias);
    return (
        <div>{categorias}</div>
    )
};

export default Categories;

export const query = graphql`
  query CategoriesQuery {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          frontmatter {
            tags
            categorias
          }
        }
      }
    }
  }
`;
